.bg-hero {
  width: 100%;
  position: relative;

  @media screen and (min-width: 1200px) {
    height: 48.6vw;
  }

  &::after {
    content: ' ';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.3;
    background-image: linear-gradient(
        243.78deg,
        #fcfdfa 45.43%,
        rgba(255, 255, 255, 0) 95.72%
      ),
      linear-gradient(181.56deg, #fefdf4 1.32%, rgba(255, 255, 255, 0) 62.39%),
      url(/src/images/hero-bg/mobile-bg.jpg);
    background-repeat: no-repeat;
    background-size: 580px 765px;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          243.78deg,
          #fcfdfa 45.43%,
          rgba(255, 255, 255, 0) 95.72%
        ),
        linear-gradient(181.56deg, #fefdf4 1.32%, rgba(255, 255, 255, 0) 62.39%),
        url(/src/images/hero-bg/mobile-bg2x.jpg);
    }

    @media (min-device-pixel-ratio: 3),
      (-webkit-min-device-pixel-ratio: 3),
      (min-resolution: 288dpi),
      (min-resolution: 3dppx) {
      background-image: linear-gradient(
          243.78deg,
          #fcfdfa 45.43%,
          rgba(255, 255, 255, 0) 95.72%
        ),
        linear-gradient(181.56deg, #fefdf4 1.32%, rgba(255, 255, 255, 0) 62.39%),
        url(/src/images/hero-bg/mobile-bg3x.jpg);
    }

    @media screen and (min-width: 768px) {
      background-image: linear-gradient(
          255.06deg,
          #fcfdfa 8.87%,
          rgba(255, 255, 255, 0) 85.66%
        ),
        linear-gradient(181.56deg, #fefdf4 1.32%, rgba(255, 255, 255, 0) 62.39%),
        url(/src/images/hero-bg/bg.jpg);
      background-size: 1289px 933px;

      @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: linear-gradient(
            255.06deg,
            #fcfdfa 8.87%,
            rgba(255, 255, 255, 0) 85.66%
          ),
          linear-gradient(
            181.56deg,
            #fefdf4 1.32%,
            rgba(255, 255, 255, 0) 62.39%
          ),
          url(/src/images/hero-bg/bg2x.jpg);
      }

      @media (min-device-pixel-ratio: 3),
        (-webkit-min-device-pixel-ratio: 3),
        (min-resolution: 288dpi),
        (min-resolution: 3dppx) {
        background-image: linear-gradient(
            255.06deg,
            #fcfdfa 8.87%,
            rgba(255, 255, 255, 0) 85.66%
          ),
          linear-gradient(
            181.56deg,
            #fefdf4 1.32%,
            rgba(255, 255, 255, 0) 62.39%
          ),
          url(/src/images/hero-bg/bg3x.jpg);
      }
    }
  }
}

.hero {
  position: relative;
  z-index: 2;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
}

.hero__img {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 32.5%;
  height: 29.25vw;
  border-top-left-radius: 40%;
  border-bottom-right-radius: 40%;
  background-image: url(../images/hero-content/desktop/hero-content-d.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  z-index: 1;

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(../images/hero-content/desktop/hero-content-d2x.jpg);
  }

  @media (min-device-pixel-ratio: 3),
    (-webkit-min-device-pixel-ratio: 3),
    (min-resolution: 288dpi),
    (min-resolution: 3dppx) {
    background-image: url(../images/hero-content/desktop/hero-content-d3x.jpg);
  }

  @media screen and (max-width: 767.5px) {
    display: none;
  }

  @media screen and (min-width: 1200px) {
    width: 40%;
    height: 36vw;
  }
}

.hero__img-mobile {
  position: relative;
  width: 100%;
  height: 87vw;
  border-top-left-radius: 40%;
  border-bottom-right-radius: 40%;
  margin-bottom: 30px;
  background-image: url(../images/hero-content/mobile/hero-content-m.jpg);
  background-size: cover;
  background-repeat: no-repeat;

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(../images/hero-content/mobile/hero-content-m2x.jpg);
  }

  @media (min-device-pixel-ratio: 3),
    (-webkit-min-device-pixel-ratio: 3),
    (min-resolution: 288dpi),
    (min-resolution: 3dppx) {
    background-image: url(../images/hero-content/mobile/hero-content-m3x.jpg);
  }

  @media screen and (min-width: 321px) {
    height: 87vw;
  }

  @media screen and (min-width: 421px) {
    height: 323px;
  }

  @media screen and (min-width: 768px) {
    display: none;
  }
}

.hero__content {
  padding-top: 25px;
  padding-bottom: 40px;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 54.5%;
    flex-grow: 1;
  }
}

.hero__text-block {
  display: flex;
  margin-bottom: 22px;
}

.hero__title {
  margin-bottom: 6px;
  font-size: 25;

  @media screen and (min-width: 768px) {
    margin-bottom: 12px;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 18px;
    font-size: 55;
  }
}

.hero__title--secondary {
  font-weight: 300;
}

.hero__text {
  color: var(--main-content-text-color);

  @media screen and (min-width: 1200px) {
    font-size: 18;
  }
}
