.container {
  width: 100%;
  margin: auto;
  padding-left: 15px;
  padding-right: 15px;

  @media screen and (min-width: 320px) {
    max-width: 482px;
  }

  @media screen and (min-width: 768px) {
    max-width: 768px;
  }

  @media screen and (min-width: 1200px) {
    max-width: 1200px;
    padding-left: 45px;
    padding-right: 45px;
  }
}

.container--biger {
  @media screen and (min-width: 1200px) {
    max-width: 1200px;
    padding-left: 5px;
    padding-right: 5px;
  }
}
.container--partners {
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
