*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

body {
  color: var(--main-text-color);
  background-color: var(--main-bg-color);
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  line-height: 1.22;
}

@media screen and (max-width: 767.5px) {
  body.is-open-menu {
    overflow: hidden;
  }
}

:root {
  --main-page-color: #1e6140;
  --secondary-page-color: #84c551;
  --main-bg-color: #f8f8f8;
  --footer-bg-color: #fff;
  --main-icon-color: #84c551;
  --secondary-icon-color: #fff;
  --page-nav-button-color: #fff;
  --main-button-color: #e74e13;
  --accent-color: #8c3213;
  --main-text-color: #000;
  --secondary-text-color: #fff;
  --main-content-text-color: rgba(0, 0, 0, .5);
  --secondary-content-text-color: rgba(255, 255, 255, .6);
  --accent-content-text-color: #1e6140;
  --price-text-color: #84be51;
}

.container {
  width: 100%;
  margin: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 482px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 45px;
    padding-right: 45px;
  }
}

@media screen and (min-width: 1200px) {
  .container--biger {
    max-width: 1200px;
    padding-left: 5px;
    padding-right: 5px;
  }
}

.container--partners {
  justify-content: space-between;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

section {
  padding-top: 40px;
  padding-bottom: 40px;
}

@media screen and (min-width: 768px) {
  section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

@media screen and (min-width: 1200px) {
  section {
    padding-top: 110px;
    padding-bottom: 120px;
  }
}

.title-area {
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .title-area {
    margin-bottom: 40px;
  }
}

.title-area--without-margin {
  margin-bottom: 0;
}

.title {
  margin: 0 9px;
  font-size: 22px;
}

@media screen and (min-width: 768px) {
  .title {
    margin: 0 17px;
  }
}

@media screen and (min-width: 1200px) {
  .title {
    font-size: 45px;
  }
}

.title--secondary-color {
  color: var(--secondary-text-color);
}

.header-auth {
  align-items: center;
  display: flex;
}

.header-auth__list {
  margin-left: 8px;
  display: flex;
}

.header-auth__item:not(:last-child):after {
  content: "";
  width: 1px;
  height: 18px;
  background-color: var(--secondary-text-color);
  margin-left: 8px;
  display: block;
}

@media screen and (min-width: 768px) {
  .header-auth__item:not(:last-child):after {
    height: 16px;
  }
}

.header-auth__item {
  border: 1px solid rgba(0, 0, 0, 0);
  align-items: center;
  padding: 3px;
  transition: border-color .2s .2s;
  display: flex;
}

.header-auth__item:focus, .header-auth__item:hover {
  border-color: var(--secondary-text-color);
}

.header-auth__item:not(:first-child) {
  margin-left: 8px;
}

.header-auth__text {
  color: var(--secondary-text-color);
  font-size: 18px;
  font-weight: 300;
  line-height: 1.22;
}

@media screen and (min-width: 768px) {
  .header-auth__text {
    font-size: 16px;
    line-height: 1.25;
  }
}

.top-header__icon {
  fill: var(--main-icon-color);
  transition: fill .2s .2s;
}

.nav {
  display: none;
}

@media screen and (min-width: 768px) {
  .nav {
    display: block;
  }
}

.nav-mobile-menu {
  border-top: 1px solid rgba(255, 255, 255, .1);
  padding: 40px 0;
}

.header-top__content .nav__list {
  flex-wrap: wrap;
  display: flex;
}

@media screen and (max-width: 1199px) {
  .header-top__content .nav__list {
    width: 90%;
    gap: 10px;
  }
}

.nav__item {
  border: 1px solid rgba(0, 0, 0, 0);
  padding: 3px;
  transition: border-color .2s .2s;
}

.nav__item:focus, .nav__item:hover {
  border-color: var(--secondary-text-color);
}

@media screen and (max-width: 1199px) {
  .nav__item {
    flex-basis: calc(33.3333% - 6.66667px);
  }
}

@media screen and (min-width: 1200px) {
  .nav__item:not(:last-child) {
    margin-right: 20px;
  }
}

.mobile-menu .nav__item {
  justify-content: center;
  display: flex;
}

.mobile-menu .nav__item:not(:last-child) {
  margin-bottom: 40px;
}

.nav__link {
  align-items: center;
  display: flex;
}

.nav__text {
  color: var(--secondary-text-color);
  font-size: 16px;
  font-weight: 300;
  line-height: 1.25;
}

.mobile-menu .nav__text {
  font-size: 22px;
  line-height: 1.23;
}

.circle-icon-wrap {
  width: 40px;
  height: 40px;
  background-color: var(--secondary-icon-color);
  border: 1px solid var(--accent-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .2s .2s;
  display: flex;
  position: relative;
}

.circle-icon-wrap:hover, .circle-icon-wrap:focus {
  background-color: var(--accent-color);
}

.contact > .circle-icon-wrap {
  margin-right: 6px;
}

@media screen and (max-width: 1199px) {
  .header-bottom__content .contact > .circle-icon-wrap {
    margin-right: 0;
  }
}

.circle-icon-wrap--card {
  width: 47px;
  height: 47px;
  border: 0;
  box-shadow: 0 0 8px rgba(0, 0, 0, .15);
}

.circle-icon-wrap--mobile-menu {
  border: 1px solid var(--secondary-icon-color);
  background-color: rgba(0, 0, 0, 0);
}

.circle-icon-wrap--mobile-menu:hover, .circle-icon-wrap--mobile-menu:focus {
  background-color: var(--main-icon-color);
}

.circle-icon {
  fill: var(--accent-color);
  transition: fill .2s .2s, background-color .2s .2s;
}

.mobile-menu .circle-icon {
  fill: var(--secondary-icon-color);
}

.circle-icon-wrap:hover .circle-icon, .circle-icon-wrap:focus .circle-icon {
  fill: #fff;
  background-color: var(--accent-color);
}

.circle-icon-wrap--mobile-menu:hover .circle-icon, .circle-icon-wrap--mobile-menu:focus .circle-icon {
  background-color: var(--main-icon-color);
}

.circle-icon__counter {
  width: 14px;
  height: 14px;
  background-color: var(--secondary-page-color);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: border .2s .2s;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.circle-icon-wrap--mobile-menu:hover .circle-icon__counter, .circle-icon-wrap--mobile-menu:focus .circle-icon__counter {
  border: 1px solid var(--secondary-icon-color);
}

.circle-icon__text {
  color: var(--secondary-text-color);
  font-size: 10px;
  line-height: 1.2;
}

.search {
  position: relative;
}

@media screen and (max-width: 767.5px) {
  .header-bottom__content .search {
    display: none;
  }
}

.mobile-menu .search {
  flex-grow: 2;
}

.search__input {
  width: 342px;
  height: 40px;
  border: 1px solid rgba(0, 0, 0, .2);
  border-radius: 20px;
  padding: 10px 55px 10px 19px;
}

.mobile-menu .search__input {
  width: 100%;
}

.search__input::-ms-input-placeholder {
  color: #484848;
}

.search__input::placeholder {
  color: #484848;
}

.search__button {
  height: 40px;
  width: 52px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  align-items: center;
  padding-left: 14px;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.search__icon {
  fill: var(--main-icon-color);
  transition: fill .2s .2s;
}

.search__button:hover .search__icon, .search__button:focus .search__icon {
  fill: var(--accent-color);
}

.mobile-menu .search__button:hover .search__icon, .search__button:focus .search__icon {
  fill: var(--main-page-color);
}

.search__button:before {
  content: " ";
  width: 1px;
  height: 28px;
  background-color: rgba(0, 0, 0, .2);
  display: block;
  position: absolute;
  left: 0;
}

.contact {
  align-items: center;
  display: flex;
}

.mobile-menu .contact {
  margin-bottom: 20px;
}

@media screen and (max-width: 767.5px) {
  .header-bottom__content .contact {
    display: none;
  }
}

@media screen and (max-width: 1199px) {
  .header-bottom__content .contact__information {
    display: none;
  }
}

.contact__text {
  align-items: baseline;
  display: flex;
}

.mobile-menu .contact__text {
  color: var(--secondary-text-color);
}

.contact__link-text {
  color: var(--price-text-color);
  font-size: 14px;
  font-weight: 700;
  line-height: 1.21;
  text-decoration: underline;
}

.button {
  width: 100%;
  height: 47px;
  background-color: var(--main-button-color);
  border: 0px solid var(--page-nav-button-color);
  color: var(--secondary-text-color);
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  padding: 5px 12px;
  transition: border .2s .2s, background-color .2s .2s;
  display: flex;
}

@media screen and (min-width: 768px) {
  .button {
    width: 220px;
    height: 47px;
    justify-content: center;
  }
}

.button--page-nav {
  border: 1px solid var(--main-page-color);
  background-color: var(--page-nav-button-color);
  color: var(--main-text-color);
  text-transform: uppercase;
  transition: border .2s .2s, background-color .2s .2s;
}

@media screen and (max-width: 767.5px) {
  .button--page-nav {
    background-color: var(--main-button-color);
    border: 0px solid var(--page-nav-button-color);
    color: var(--secondary-text-color);
    justify-content: space-between;
  }
}

@media screen and (min-width: 768px) {
  .button--page-nav {
    width: 200px;
    height: 40px;
  }
}

.button--page-nav:hover, .button--page-nav:focus {
  background-color: var(--main-button-color);
  border: 0px solid var(--page-nav-button-color);
}

@media screen and (min-width: 768px) {
  .button--hero {
    position: relative;
    left: 44px;
  }
}

.button-content {
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .button--about {
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .button--center {
    margin: 0 auto;
  }
}

.button__icon {
  width: 20px;
  height: 20px;
  fill: var(--main-icon-color);
  margin-right: 6px;
  transition: fill .2s .2s;
}

.button--page-nav:hover .button__icon, .button--page-nav:focus .button__icon, .button__icon:focus, .button__icon:hover {
  fill: var(--secondary-icon-color);
}

@media screen and (max-width: 767.5px) {
  .button__icon {
    fill: var(--secondary-icon-color);
  }
}

@media screen and (min-width: 768px) {
  .button__icon {
    width: 24px;
    height: 24px;
    margin-right: 14px;
  }
}

.button__text {
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21;
  transition: color .2s .2s;
}

.button--page-nav:hover .button__text, .button--page-nav:focus .button__text {
  color: var(--secondary-text-color);
}

.leaf {
  fill: var(--main-icon-color);
}

@media screen and (min-width: 768px) {
  .leaf {
    width: 32px;
    height: 25px;
  }
}

.leaf--right {
  transform: scaleX(-1);
}

.leaf--hero {
  flex-shrink: 0;
  margin-right: 12px;
  position: relative;
  top: 28px;
}

.first-wing {
  filter: blur(2px);
  pointer-events: none;
  position: absolute;
  top: -14.5%;
  right: 7.6%;
  transform: scaleX(-1);
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .first-wing {
    width: 124px;
    height: 89px;
  }
}

.two-wings {
  position: absolute;
  bottom: -17%;
  left: -15.5%;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .second-wing {
    width: 124px;
    height: 89px;
  }
}

.third-wing {
  filter: blur(2px);
  position: relative;
  bottom: 75%;
  left: 50%;
  transform: translate(-50%, -75%)scaleX(-1);
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .third-wing {
    width: 76px;
    height: 55px;
  }
}

.about__big-wing {
  position: absolute;
  top: 52%;
  left: -7.7%;
}

@media screen and (max-width: 767.5px) {
  .about__big-wing {
    display: none;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .about__big-wing {
    width: 124px;
    height: 89px;
  }
}

.about__small-wing {
  filter: blur(2px);
  position: absolute;
  bottom: -3.7%;
  left: 13%;
  transform: scaleX(-1);
}

@media screen and (max-width: 767.5px) {
  .about__small-wing {
    display: none;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .about__small-wing {
    width: 76px;
    height: 55px;
  }
}

.product-card {
  width: 235px;
  height: 355px;
  background: #fff;
  border-radius: 20px;
  padding: 10px;
  transition: box-shadow .2s .2s;
  position: relative;
}

.product-card:hover, .product-card:focus {
