.menu-button {
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  display: flex;
}

@media screen and (min-width: 768px) {
  .menu-button {
    display: none;
  }
}

